// import Rili from "../common/dark-calendar/dark-calendar.vue"
import wCalendar from "../common/w-calendar/w-calendar.vue";
export default{
	components:{
		// Rili,
		wCalendar
	},
	data(){
		return{
			result:{},
			time:null,
			id:null,
			recordList:[]
		}
	},
	mounted(){
		this.$refs.calendar.show();
	},
	onLoad(e){
		this.id=e.id
		console.log(e.id)
		let date=new Date()
		let dateStr=""
		dateStr=date.getFullYear()+"-"
		let month=date.getMonth()+1
		month=month<10?"0"+month:month
		dateStr+=month+"-"
		let day=date.getDate()
		day=day<10?"0"+day:day
		dateStr+=day
		this.time=dateStr
		this.getRecord()
	},
	methods:{
		getResult(val){
			console.log(val)
			this.time=val.fullDate
			this.getRecord()
			// this.result=val;
			// this.$refs.calendar.show();
		},
		getRecord(){
			// 获取行程记录
			this.$http.post("sRidingRecord/list",{
				securityId:this.id,
				fromTime:this.time,
				toTime:this.time
			}).then(res=>{
				if(res.code==100){
					this.recordList=res.info
				}
			})
		},
		lookDetails(id){
			uni.navigateTo({
				url:"details?id="+id
			})
		},
	}
}
